    /**
     * @param {?} month
     * @return {?}
     */
    AmexioDateTimePicker.prototype.getDropdownMonth = /**
     * @param {?} month
     * @return {?}
     */
    function (month) {
        this.monthList1.forEach(function (element) {
            if (element.flag)
                element.flag = false;
        });
        this.monthList2.forEach(function (element) {
            if (element.flag)
                element.flag = false;
        });
        this.monthList1.forEach(function (element) {
            if (element.name == month.name) {
                element.flag = true;
            }
        });
        this.monthList2.forEach(function (element) {
            if (element.name == month.name) {
                element.flag = true;
            }
        });
        switch (month.name) {
            case "Jan":
                this.monthNo = 0;
                break;
            case "Feb":
                this.monthNo = 1;
                break;
            case "Mar":
                this.monthNo = 2;
                break;
            case "Apr":
                this.monthNo = 3;
                break;
            case "May":
                this.monthNo = 4;
                break;
            case "Jun":
                this.monthNo = 5;
                break;
            case "Jul":
                this.monthNo = 6;
                break;
            case "Aug":
                this.monthNo = 7;
                break;
            case "Sep":
                this.monthNo = 8;
                break;
            case "Oct":
                this.monthNo = 9;
                break;
            case "Nov":
                this.monthNo = 10;
                break;
            case "Dec":
                this.monthNo = 11;
                break;
            default: break;
        }
        this.monthNo;
    };
